<template>
  <div>
    <div style="display: inline-grid">
      <button
        @click="withHeapMemoryIncrease()"
        style="padding: 10px; margin: 15px 0px"
      >
        Increase Heap
      </button>
      <button
        @click="noHeapMemoryIncrease()"
        style="padding: 10px; margin: 15px 0px"
      >
        Constant Heap
      </button>
      <button
        @click="combinedMethods()"
        style="padding: 10px; margin: 15px 0px"
      >
        Combined Function
      </button>
      <button @click="finalApproach()" style="padding: 10px; margin: 15px 0px">
        Final Approach
      </button>
      <button @click="reset()" style="padding: 10px; margin: 15px 0px">
        Reset
      </button>
      <div v-for="(item, index) in dataList" :key="index">
        {{ item }}
      </div>
    </div>
    <!-- <reactive-update /> -->
    <!-- <non-reactive-update /> -->
  </div>
</template>
<script>
// import NonReactiveUpdate from "./NonReactiveUpdate.vue";
// import ReactiveUpdate from "./ReactiveUpdate.vue";
export default {
  // components: { NonReactiveUpdate, ReactiveUpdate },
  data() {
    return {
      interval: null,
      dataList: [],
    };
  },
  methods: {
    withHeapMemoryIncrease() {
      var theThing = null;
      var that = this;
      var replaceThing = function () {
        var originalThing = theThing;
        theThing = {
          longStr: new Array(1000000).join("*"),
        };
        that.dataList.push(originalThing);
      };
      this.interval = setInterval(replaceThing, 5000);
    },
    noHeapMemoryIncrease() {
      var theThing = null;
      var replaceThing = function () {
        var originalThing = theThing;
        theThing = {
          longStr: new Array(1000000).join("*"),
          someMethod: function () {
            console.log(originalThing);
          },
        };
        // theThing.someMethod();
      };
      this.interval = setInterval(replaceThing, 1000);
    },
    combinedMethods() {
      var theThing = null;
      var replaceThing = function () {
        console.log("replaceThing");
        var originalThing = theThing;
        var unused = function () {
          if (originalThing) console.log("hi");
        };
        theThing = {
          longStr: new Array(1000000).join("*"),
          someMethod: function () {
            console.log(unused);
          },
        };
      };
      this.interval = setInterval(replaceThing, 1000);
    },
    finalApproach() {
      var theThing = null;
      var replaceThing = function () {
        var originalThing = theThing;
        // Define a closure that references originalThing but doesn't ever
        // actually get called. But because this closure exists,
        // originalThing will be in the lexical environment for all
        // closures defined in replaceThing, instead of being optimized
        // out of it. If you remove this function, there is no leak.
        var unused = function () {
          if (originalThing) console.log("hi");
        };
        theThing = {
          longStr: new Array(1000000).join("*"),
          // While originalThing is theoretically accessible by this
          // function, it obviously doesn't use it. But because
          // originalThing is part of the lexical environment, someMethod
          // will hold a reference to originalThing, and so even though we
          // are replacing theThing with something that has no effective
          // way to reference the old value of theThing, the old value
          // will never get cleaned up!
          someMethod: function () {
            console.log("unused", unused());
          },
        };
        // If you add `originalThing = null` here, there is no leak.
      };
      this.interval = setInterval(replaceThing, 1000);
    },
    reset() {
      clearInterval(this.interval);
    },
  },
};
</script>
